.post {
  max-width: 700px;
  margin: 20px auto;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.post h1 {
  margin-bottom: 16px;
  font-size: 28px;
  color: #333;
}

.post p {
  line-height: 1.6;
  margin-bottom: 16px;
  color: #555;
}
.post input {
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
}
.post button {
  margin-top: 15px;
  padding: 8px 12px;
  background: #0077cc;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.post button:hover {
  background: #005fa3;
}